@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Roboto google font */


* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
*:focus {
    outline: none;
}

body {
    font-family: Samim-FD-WOL;
    background-color: #fafbfb;
}
@font-face {
    font-family: Samim-FD-WOL;
    src: url(/static/media/ArbFONTS-Samim-FD-WOL.82b4a9c4.ttf)
}
@font-face {
    font-family: DINNextLTArabic;
    src: url(/static/media/DINNextLTArabic.29b1418d.ttf)
}

a {
    text-decoration: none;
    
}
a:hover {
    text-decoration: none;
}

ul, p, h1, h2, h3, h4, h5, h6 {
    margin-bottom: 0px;
}
ul li{
    list-style: none;
}
.container {
    max-width: 1240px;
}
.mynav{
    width: 100%;
    height: 65px;
    font-family: Samim-FD-WOL!important;
    font-size: 16px !important;
    font-weight: normal;
    color: rgba(16, 24, 32, 0.65) !important;
    padding: 0;
    background-color: #ffffff;
    display: flex;
    align-items: center;
}


textarea:hover,
input:hover,
select:hover,
textarea:active,
input:active,
select:active,
textarea:focus,
input:focus,
select:focus
 {
    outline: 0px !important;
    -webkit-appearance: none;
    box-shadow: none !important;
}

input:focus,
textarea:focus,
select:focus {
    outline: none !important;
}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    font-family: Samim-FD-WOL !important;
    font-size: 14px !important;
    font-weight: normal !important;
    color: rgba(0, 0, 0, 0.25)!important;
    
}

input:-ms-input-placeholder, textarea:-ms-input-placeholder {
    font-family: Samim-FD-WOL !important;
    font-size: 14px !important;
    font-weight: normal !important;
    color: rgba(0, 0, 0, 0.25)!important;
    
}

input::-ms-input-placeholder, textarea::-ms-input-placeholder {
    font-family: Samim-FD-WOL !important;
    font-size: 14px !important;
    font-weight: normal !important;
    color: rgba(0, 0, 0, 0.25)!important;
    
}

input::placeholder,
textarea::placeholder {
    font-family: Samim-FD-WOL !important;
    font-size: 14px !important;
    font-weight: normal !important;
    color: rgba(0, 0, 0, 0.25)!important;
    
}

button {
    outline: none;
}
button:focus {
    outline: none;
}
.MuiAlert-message {
    font-family: Samim-FD-WOL !important;
    font-size: 14px;
    
}

input[type=email],
input[type=password],
.cloneCount {
    font-family: DINNextLTArabic !important;
    font-size: 16px;
    font-weight: normal;
    
}

.dashboard,
.ant-form-item-label>label,
.ant-btn>span {
    font-family: 'Roboto', Arial, Helvetica, sans-serif !important;
}

